#multipleChoice {
  fieldset {
    margin: 40px 0;
    > label {
      color: $darkblue;
      font-family: $headingfont;
      font-size: 2em; } }
  .multiChoiceAnswer {
    margin: 10px 0;
    &:first-of-type {
      margin-top: 20px; } }
  input[type="radio"] {
    display: none; }
  input[type="radio"] {
    + label {
      position: relative;
      top: 3px;
      display: inline-block;
      background:  no-repeat center / contain url('../img/starfish-darkblue.svg');
      width: 20px;
      height: 20px;
      margin-right: 10px;
      font-size: 20px;
      color: $darkblue;
      text-align: center;
      &::after {
        content: "Â·";
        color: transparent; } }
    ~ label {
      font-size: 1.4em;
      color: $darkblue;
      line-height: 26px; }
    &.correct + label {
      background-image: url('../img/starfish-lightblue.svg'); }
    &.correct ~ label {
      border-color: $lightblue;
      color: $lightblue; }
    &.wrong + label {
      background-image: url('../img/starfish-orange.svg'); }
    &.wrong ~ label {
      border-color: $orange;
      color: $orange; }
    ~ label {
      cursor: pointer; } }
  .scoreMsg {
    max-width: 80%;
    margin: 0 auto 80px;
    .msgTitle {
      font-family: $headingfont;
      font-size: 3em;
      color: $lightblue; }
    p {
      font-size: 1.4em; } } }
